@font-face {
  font-family: 'SourceSansProBold';
  src: url('./fonts/SourceSansPro-Bold.ttf');
}

@font-face {
  font-family: 'SourceSansProLight';
  src: url('./fonts/SourceSansPro-Light.ttf');
}

body {
  margin: 0;
  background-color: #212931;
  font-family: Arial, Helvetica, sans-serif;
}

#root {
  margin-top: 60px;
}

Nav {
  position: fixed;
  z-index: 1;
  width: 100%;
  top: 0;
  line-height: 60px;
  font-family: 'SourceSansProLight';
  font-weight: bold;
  background-color: black;
  color: white;
}

.navlinks {
  display: flex;
  justify-content: space-around;
  list-style: none;
  font-size: 18px;
  margin: 0;
  padding: 0;
}

.navlinks a {
  text-decoration: none;
  color: white;
  width: 100%;
  text-align: center;
}

.navlinks a:hover {
  background-color: #444;
}

#home {
  font-family: 'SourceSansProLight';
  color: white;
  width: 80%;
  max-width: 1400px;
  padding-top: 50px;
  margin: 0 auto;
  text-align: center;
}

#header {
  font-size: 5rem;
  margin: 0;
}

#headerText {
  font-size: 2em;
  margin-bottom: 80px;
}

#content {
  font-family: 'SourceSansProBold';
  background-color: white;
  color: black;
  margin-bottom: 100px;
}

.contentHeader {
  font-size: 4em;
  margin: 0;
  padding: 30px 0;
}

#skillsContainer {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.skillsColumn h2 {
  font-size: 2em;
}

.skillsColumn p {
  font-family: 'SourceSansProLight';
  font-size: 18px;
}

#projectsContainer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.project {
  padding: 20px;
  border-left: 2px solid #eeeeee;
  border-top: 2px solid #eeeeee;
}

.project h1 {
  margin: 0;
  margin-bottom: 20px;
}

.gif {
  display: flex;
  position: relative;
  margin: 0 auto;
  padding: 0;
  border: 0;
  max-width: 80%;
  background-color: white;
  outline: none;
}

.gif:hover::after {
  content: '';
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}

.gif.one:hover::after {
  background-image: url('images/connectu.gif');
}

.gif.two:hover::after {
  background-image: url('images/battleship.gif');
}

.gif.three:hover::after {
  background-image: url('images/cardboardluxury.gif');
}

.gif.four:hover::after {
  background-image: url('images/weatherapp.gif');
}

.gif.five:hover::after {
  background-image: url('images/calculator.gif');
}

.gif.six:hover::after {
  background-image: url('images/memorycard.gif');
}

.project img {
  max-width: 100%;
}

.githubLink {
  display: block;
  margin: 20px auto;
  max-width: 50px;
  max-height: 50px;
  color: black;
}

.githubLink:hover {
  color: #1797bb;
}

.viewLink {
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  font-size: 13px;
  color: black;
  text-decoration: none;
  width: 100px;
  height: 40px;
  padding: 0;
  margin: 0 auto;
  border: 2px solid black;
  background-color: white;
  font-family: 'SourceSansProBold';
}

.viewLink:hover {
  color: #1797bb;
  border-color: #1797bb;
}

#homeFooter {
  padding: 50px 0;
  border-top: 2px solid #eeeeee;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

#contactmeLink {
  flex: 1;
}

#contactmeLink button,
#sendMessageButton,
#resume a {
  transition: all ease .3s;
  outline: none;
  cursor: pointer;
  border-radius: 1000px;
  background: #4b6cc1;
  border: 0;
  color: #fff;
  font-size: 1.25em;
  padding: 17px 30px;
}

#contactmeLink button:hover,
#sendMessageButton:hover,
#resume a:hover {
  transform: scale(1.1);
}

#footerLinks {
  flex: 1;
  display: flex;
  flex-direction: column;
  font-size: 18px;
  text-align: left;
}

#footerLinks a {
  margin: 0;
  margin-bottom: 20px;
}

#footerLinks a {
  text-decoration: none;
  color: black;
}

#footerLinks a:hover {
  color: #1797bb;
}

#footerLinks svg {
  max-width: 20px;
  color: black;
  vertical-align: middle;
}

#footerLinks span {
  margin-left: 10px;
  vertical-align: middle;
}

#contact {
  width: 90%;
  max-width: 650px;
  margin: 0 auto;
  padding-top: 50px;
  text-align: center;
}

#contactHeader {
  font-family: 'SourceSansProLight';
  color: white;
  font-size: 5em;
  margin: 0;
}

#infoInput {
  display: flex;
  justify-content: space-between;
  margin: 30px 0;
}

#sendMessageButton {
  display: block;
  margin: 0 auto;
  margin-bottom: 50px;
}

#contact input,
#contact textarea {
  font-family: 'SourceSansProLight';
  font-weight: bold;
  font-size: 1.25rem;
  box-sizing: border-box;
  outline: none;
  padding: 15px;
  border-radius: 6px;
  border: 2px solid #e2e2e2;
  width: 100%;
  background: #f9f9f9;
  color: #191a1d;
}

#contact textarea {
  resize: vertical;
  max-width: 650px;
  min-height: 200px;
  margin-bottom: 30px;
}

#loadingScreen {
  display: block;
  position: fixed;
  z-index: 3;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: #00000073;
}

#spinningCircle {
  border: 5px solid #f3f3f3;
  border-top: 5px solid #555;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  position: relative;
  top: 270px;
  margin: 0 auto;
  animation: spin 1.5s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.fieldBox div {
  color: red;
  font-family: 'SourceSansProBold';
  font-size: 18px;
  margin: 5px 0;
}

#success {
  padding-top: 50px;
  text-align: center;
  color: white;
}

#success h1 {
  font-size: 3em;
}

#success h2 {
  font-size: 2em;
}

#success svg {
  max-width: 50px;
  color: green;
}

#resume {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#resume img {
  width: 95vw;
  max-width: 700px;
}

#resume a {
  text-decoration: none;
  margin: 40px 0;
}

@media (max-width:1024px) {
  .gif {
    max-width: 100%;
  }
}

@media (max-width:700px) {
  #projectsContainer {
    grid-template-columns: 1fr;
  }

  #header {
    font-size: 4rem;
  }

  #headerText {
    margin-bottom: 32px;
  }

  .contentHeader {
    font-size: 3em;
  }

  .skillsColumn h2 {
    font-size: 1.5em;
  }

  #homeFooter {
    flex-direction: column;
    align-items: stretch;
  }

  #footerLinks {
    margin-top: 40px;
    margin-left: 5%;
  }

  #contactHeader {
    font-size: 4rem;
  }

  #infoInput {
    flex-direction: column;
  }

  .fieldBox:first-child {
    margin-bottom: 10px;
  }

  #success h1 {
    font-size: 2em;
  }
  
  #success h2 {
    font-size: 1.5em;
  }
}

@media (max-width:425px) {
  #home {
    width: 95%;
  }

  #header {
    font-size: 3rem;
  }

  #headerText {
    font-size: 1.5em;
  }

  .contentHeader {
    font-size: 2em;
  }

  .skillsColumn h2 {
    font-size: 1em;
  }

  .skillsColumn p {
    font-size: 16px;
  }

  #contactHeader {
    font-size: 3rem;
  }
}

@media (max-width:320px) {
  #footerLinks {
    font-size: 14px;
  }
}